body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.dashBtn:hover {
    opacity: .5;
}

.jumboBg {
    background-image: url("https://cdn.stocksnap.io/img-thumbs/280h/VP6XCPG662.jpg");
    border-radius: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.4);
}

.jumboText {
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(0, 0, 0, 0.4);
    border-radius: 20px;
}

h1 {
    color: white;
    font-weight: bold;
    
}

.jumboP {
    color: white;
    font-weight: bold;
    
}
.wrapper {
    display: flex;
    
    align-items: stretch;
}


#sidebar {
    min-width: 250px;
    max-width: 250px;
    min-height: 100vh;
}

#sidebar.active {
    margin-left: -250px;
}

a[data-toggle="collapse"] {
    position: relative;
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

@media (max-width: 768px) {
    #sidebar {
        margin-left: -250px;
    }
    #sidebar.active {
        margin-left: 0;
    }
}


body {
    font-family: 'Poppins', sans-serif;
    background: #fafafa;
    /* margin-bottom: 100px; */
    clear: both;
}

p {
    font-family: 'Poppins', sans-serif;
    font-size: 1.1em;
    font-weight: 300;
    line-height: 1.7em;
    /* color: #999; */
    font-size:15px;
    color: #000;
    /* text-shadow:
		-1px -1px 0 #fff,
		1px -1px 0 #fff,
		-1px 1px 0 #fff,
		1px 1px 0 #fff; */
}

.user-name {
    color: #fff;
    text-shadow:
		-1px -1px 0 #000,
		1px -1px 0 #000,
		-1px 1px 0 #000,
		1px 1px 0 #000;
}

a, a:hover, a:focus {
    color: inherit;
    text-decoration: none;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

#sidebar {
    /* don't forget to add all the previously mentioned styles here too */
    background: #1c1d1f;
    color: #fff;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

#sidebar .sidebar-header {
    padding: 20px;
    background: #393f4d;
}

#sidebar ul.components {
    padding: 20px 0;
    border-bottom: 1px solid #1c1d1f;
}

#sidebar ul p {
    color: #fff;
    padding: 10px;
}

#sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
}
#sidebar ul li a:hover {
    color: #4e5b74;
    background: #ffd96a;
}

#sidebar ul li.active > a, a[aria-expanded="true"] {
    color: #4e5b74;
    background: #ffd96a;
}
ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #000000;
}

.main {
    padding-top: 2.5%;
}


h2 {
    text-align: center;
    line-height: 75px;
}
.lead{
    text-indent: 50px;
    padding-top: 50px;
    padding-bottom: 30px;
    padding-left: 100px;
    padding-right: 100px;
    line-height: 50px;
}



